.promo__donate {
	&-form {
		&__uploadMenu {
			position: fixed;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1000;

			width: 690px;
			height: 413px;
			background-color: rgb(49, 197, 161, 1);

			border-radius: 10px;
			box-shadow: rgba(0, 0, 0, 0.5);
			padding: 5px;

			&-wrapper {
				display: flex;
				flex-direction: column;

				height: 100%;
			}

			&-closeBtn {
				align-self: flex-end;

				width: 39px;
				height: 39px;

				border: none;
				border-radius: 100%;
				background-color: rgba(255, 80, 80, 0.62);

				color: white;
				font-size: 18px;

				transition: all 2s ease-in-out;
				&:hover {
					background-color: rgba(255, 0, 0, 0.62);
					transform: scale(1.1);
				}
			}

			&-fileBtn {
				margin: auto;
				width: 553px;
				height: 82px;
				background-color: transparent;

				border: 2px dashed rgba(128, 128, 128, 0.9);
				border-radius: 11px;

				color: #808080;
				font-size: 35px;
				font-weight: 900;
			}
		}
    
		&__inputs {
			width: 563px;
			height: 269px;
			border-radius: 9px;

			background-color: rgba(49, 197, 161, 1);
			box-shadow: 1px 4px 3px rgba(0, 0, 0, 0.5);

			display: flex;
			padding-left: 10px;
			align-items: center;
		}

		&__input {
			display: flex;
			gap: 15px;

			&-label {
				min-width: 229px;

				font-size: 34px;
				font-weight: 900;
				line-height: 50px;
			}

			&-field {
				align-self: center;

				width: 292px;
				min-height: 27px;
				max-height: 35px;

				border: none;
				border-radius: 5px;

				background: rgba(206, 211, 223, 0.55);
				resize: vertical;
			}
		}

		&__button {
			margin-top: 20px;
			width: 263.72px;
			height: 80.979px;
			background: #a999ea;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.35);
			border-radius: 10px;
			border: none;

			color: #180172;
			font-size: 35px;
			font-weight: 600;
			line-height: normal;

			transition: all 6s ease-in-out;

			&:hover {
				background-color: yellow;
				transform: scale(1.1);
			}
		}
  }
}

    


.promo__donate-from__error {
	line-height: 2px;
	color: red;
	text-align: end;
}


